"use client";

import { useEffect, useState } from "react";

const WORKSPACE_ID = "698c41f69544ffb5052a58c4"; // replace once

export default function InboxPage() {
  const [conversations, setConversations] = useState([]);
  const [selectedConversation, setSelectedConversation] = useState(null);
  const [messages, setMessages] = useState([]);
  const [reply, setReply] = useState("");

  // 1️⃣ Fetch inbox conversations
  useEffect(() => {
    fetch(`http://localhost:4000/api/inbox/${WORKSPACE_ID}`)
      .then((res) => res.json())
      .then((data) => setConversations(data))
      .catch(console.error);
  }, []);

  // 2️⃣ Fetch messages for selected conversation
  const loadMessages = (conversationId) => {
    setSelectedConversation(conversationId);
    fetch(
      `http://localhost:4000/api/inbox/conversation/${conversationId}`
    )
      .then((res) => res.json())
      .then((data) => setMessages(data))
      .catch(console.error);
  };

  // 3️⃣ Send staff reply
  const sendReply = async () => {
    if (!reply) return;

    await fetch(
      `http://localhost:4000/api/inbox/conversation/${selectedConversation}/reply`,
      {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ message: reply }),
      }
    );

    setReply("");
    loadMessages(selectedConversation);
  };

  return (
    <div style={{ display: "flex", height: "100vh" }}>
      {/* LEFT: Conversation List */}
      <div style={{ width: "30%", borderRight: "1px solid #ccc" }}>
        <h3>Inbox</h3>

        {conversations.map((conv) => (
          <div
            key={conv.conversationId}
            style={{
              padding: "10px",
              cursor: "pointer",
              borderBottom: "1px solid #eee",
            }}
            onClick={() => loadMessages(conv.conversationId)}
          >
            <strong>{conv.contact.name}</strong>
            <p style={{ fontSize: "12px" }}>
              {conv.lastMessage?.content}
            </p>
          </div>
        ))}
      </div>

      {/* RIGHT: Messages */}
      <div style={{ width: "70%", padding: "10px" }}>
        {!selectedConversation ? (
          <p>Select a conversation</p>
        ) : (
          <>
            <div style={{ height: "80%", overflowY: "auto" }}>
              {messages.map((msg, idx) => (
                <div key={idx}>
                  <strong>{msg.sender}:</strong> {msg.content}
                </div>
              ))}
            </div>

            <div style={{ marginTop: "10px" }}>
              <input
                value={reply}
                onChange={(e) => setReply(e.target.value)}
                placeholder="Type reply..."
                style={{ width: "80%" }}
              />
              <button onClick={sendReply}>Send</button>
            </div>
          </>
        )}
      </div>
    </div>
  );
}
